import React from "react";

// reactstrap components
import {
    Button,
    FormGroup,
    Input,
    Container,
    Row,
    Col
} from "reactstrap";

// core components
import ExamplesNavbar from "components/Navbars/DashBoardNavbar.js";
import Footer from "components/Footer/Footer.js";
import axios from "axios/index";
import NewsTabs from "../../components/NewsTabs/newsTab";

class HomePage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            searchKey: "",
            searchData: []
        };
    }

    componentDidMount() {
        document.body.classList.toggle("landing-page");
        axios.get("http://localhost:8000/news/search?source=google-news").then(res => {
            if (res.status === 200) {
                this.setState({searchData: res.data.articles});
            }
        });
    }

    componentWillUnmount() {
        document.body.classList.toggle("landing-page");
    }

    onChangeSearchKey = e => {
        this.setState({searchKey: e.target.value});
        console.log(this.state)
    };

    onClickEvent = e => {
        e.preventDefault();
        if (this.state.searchKey !== "") {
            axios.get("http://localhost:8000/news/search?searchKey=" + this.state.searchKey).then(res => {
                if (res.status === 200) {
                    this.setState({searchData: res.data.articles});
                }
            });
        }
    };

    render() {
        return (
            <>
                <ExamplesNavbar/>
                <div className="wrapper">
                    <section className="section section-lg">
                        <section className="section">
                            <img
                                alt="..."
                                className="path"
                                src={require("assets/img/path4.png")}
                            />
                            <Container>
                                <Row className="row-grid justify-content-between">
                                    <Col md="12">
                                        <div className="pl-md-5">
                                            <div className="content-center">
                                                <Row className="row-grid justify-content-between">
                                                    <Col lg="10" md="6">

                                                        <FormGroup className="btn-wrapper mb-3">
                                                            <Input
                                                                placeholder="search here...."
                                                                type="text"
                                                                value={this.state.searchKey}
                                                                onChange={this.onChangeSearchKey}
                                                            />
                                                        </FormGroup>
                                                    </Col>
                                                    <Col lg="2" md="6">
                                                        <div className="btn-wrapper mb-3">
                                                            <Button
                                                                className="btn-link"
                                                                color="success"
                                                                href="#pablo"
                                                                onClick={this.onClickEvent}
                                                                size="sm"
                                                            >
                                                                Search
                                                                <i className="tim-icons icon-minimal-right"/>
                                                            </Button>
                                                        </div>
                                                    </Col>
                                                </Row>
                                            </div>
                                            <br/>
                                        </div>
                                    </Col>
                                </Row>
                                <NewsTabs searchData={this.state.searchData}/>
                            </Container>
                        </section>
                    </section>

                    <Footer/>
                </div>
            </>
        );
    }
}

export default HomePage;
