import React from "react";
import classnames from "classnames";
// reactstrap components
import {
    TabContent,
    TabPane,
    Container,
    Row,
    Col,
    Card,
    CardHeader,
    CardBody,
    Nav,
    NavItem,
    NavLink
} from "reactstrap";

class NewsTabs extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            iconTabs: 1,
        };
    }

    toggleTabs = (e, stateName, index) => {
        e.preventDefault();
        this.setState({
            [stateName]: index
        });
    };

    prepareTabs = () => {
        let columns = [];
        console.log(this.props.searchData);
        for (const [index, value] of this.props.searchData.entries()) {
            let cardHeader =
                <CardHeader>
                    <Nav className="nav-tabs-info" role="tablist" tabs>
                        <NavItem>
                            <NavLink
                                className={classnames({
                                    active: this.state.iconTabs === 1
                                })}
                                onClick={e => this.toggleTabs(e, "iconTabs", 1)}
                                href="#pablo"
                            >
                                <i className="tim-icons icon-spaceship"/>
                                News
                            </NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink
                                className={classnames({
                                    active: this.state.iconTabs === 2
                                })}
                                onClick={e => this.toggleTabs(e, "iconTabs", 2)}
                                href="#pablo"
                            >
                                <i className="tim-icons icon-settings-gear-63"/>
                                details
                            </NavLink>
                        </NavItem>
                    </Nav>
                </CardHeader>;
            let cardBody =
                <CardBody>
                    <TabContent
                        className="tab-space"
                        activeTab={"link" + this.state.iconTabs}
                    >
                        <TabPane tabId="link1">
                            <h3>{value.title}</h3>
                            <blockquote>
                                <p className="blockquote blockquote-info">
                                    {value.description} <br/>
                                    <br/>
                                    <small>- {value.author}</small>
                                </p>
                            </blockquote>
                            <img alt="" src={value.urlToImage}/>
                        </TabPane>
                        <TabPane tabId="link2">

                            <span>-Source</span><p className="text-info">{value.Source.name}</p>
                            <tspan>-Author</tspan>
                            <p className="text-info">{value.author}</p>
                            <tspan>-sourceUrl</tspan>
                            <p><a href={value.url}>Source URL..</a><i className="tim-icons icon-minimal-right"/></p>
                            <tspan>-publishedAt</tspan>
                            <p className="text-info">{value.publishedAt}</p>
                        </TabPane>
                    </TabContent>
                </CardBody>;
            columns.push(
                <Col className="ml-auto mr-auto" md="10" xl="6">
                    <Card>
                        {cardHeader}
                        {cardBody}
                    </Card>
                </Col>)
        }
        return columns
    };

    render() {
        return (
            <div className="section section-tabs">
                <img
                    alt="..."
                    className="path"
                    src={require("assets/img/path3.png")}
                />
                <Container>
                    <Row>
                        {this.prepareTabs()}
                    </Row>
                </Container>
            </div>
        );
    }

}

export default NewsTabs;
