import React from "react";
import classnames from "classnames";
// reactstrap components
import {
    Button,
    Card,
    CardHeader,
    CardBody,
    CardFooter,
    CardImg,
    CardTitle,
    Label,
    FormGroup,
    Form,
    Input,
    InputGroupAddon,
    InputGroupText,
    InputGroup,
    Container,
    Row,
    Col
} from "reactstrap";

// core components
import ExamplesNavbar from "components/Navbars/IndexNavbar.js";
import Footer from "components/Footer/Footer.js";
import {Link, Redirect} from "react-router-dom";
import axios from "axios/index";

class RegisterPage extends React.Component {
    state = {
        squares1to6: "",
        squares7and8: "",
        name: "",
        email: "",
        password: "",
        termsAgreed: false,
        redirect: false

    };

    componentDidMount() {
        document.body.classList.toggle("register-page");
        document.documentElement.addEventListener("mousemove", this.followCursor);
    }

    componentWillUnmount() {
        document.body.classList.toggle("register-page");
        document.documentElement.removeEventListener(
            "mousemove",
            this.followCursor
        );
    }

    followCursor = event => {
        let posX = event.clientX - window.innerWidth / 2;
        let posY = event.clientY - window.innerWidth / 6;
        this.setState({
            squares1to6:
            "perspective(500px) rotateY(" +
            posX * 0.05 +
            "deg) rotateX(" +
            posY * -0.05 +
            "deg)",
            squares7and8:
            "perspective(500px) rotateY(" +
            posX * 0.02 +
            "deg) rotateX(" +
            posY * -0.02 +
            "deg)"
        });
    };

    // Form Events
    onChangeName = event => {
        this.setState({name: event.target.value});
    };

    onChangeEmail = e => {
        this.setState({email: e.target.value});
    };
    onChangePassword = e => {
        this.setState({password: e.target.value});
    };

    onChangeTerms = e => {
        this.setState({termsAgreed: !this.state.termsAgreed});
    };

    //axios call
    handleSubmit = event => {
        event.preventDefault();
        if (this.state.password !== "" && this.state.email !== "" && this.state.name !== "" && this.state.termsAgreed !== false) {
            axios.post('http://localhost:8000/user/create', {
                name: this.state.name,
                email: this.state.email,
                password: this.state.password
            }).then(res => {
                if (res.status === 200) {
                    console.log(res.data);
                    this.setState({redirect: true});
                }
            });
        }

    };

    render() {
        return (
            <>
                <ExamplesNavbar/>
                <div className="wrapper">
                    <div className="page-header">
                        <div className="page-header-image"/>
                        <div className="content">
                            <Container>
                                <Row>
                                    <Col className="offset-lg-0 offset-md-3" lg="5" md="6">
                                        <div
                                            className="square square-7"
                                            id="square7"
                                            style={{transform: this.state.squares7and8}}
                                        />
                                        <div
                                            className="square square-8"
                                            id="square8"
                                            style={{transform: this.state.squares7and8}}
                                        />
                                        <Card className="card-register">
                                            <CardHeader>
                                                <CardImg
                                                    alt="..."
                                                    src={require("assets/img/square-purple-1.png")}
                                                />
                                                <CardTitle tag="h4">Register</CardTitle>
                                            </CardHeader>
                                            <CardBody>
                                                <Form className="form">
                                                    <InputGroup
                                                        className={classnames({
                                                            "input-group-focus": this.state.fullNameFocus
                                                        })}
                                                    >
                                                        <InputGroupAddon addonType="prepend">
                                                            <InputGroupText>
                                                                <i className="tim-icons icon-single-02"/>
                                                            </InputGroupText>
                                                        </InputGroupAddon>
                                                        <Input
                                                            placeholder="Full Name"
                                                            type="text"
                                                            onFocus={e =>
                                                                this.setState({fullNameFocus: true})
                                                            }
                                                            onBlur={e =>
                                                                this.setState({fullNameFocus: false})
                                                            }
                                                            value={this.state.name}
                                                            onChange={this.onChangeName}
                                                        />
                                                    </InputGroup>
                                                    <InputGroup
                                                        className={classnames({
                                                            "input-group-focus": this.state.emailFocus
                                                        })}
                                                    >
                                                        <InputGroupAddon addonType="prepend">
                                                            <InputGroupText>
                                                                <i className="tim-icons icon-email-85"/>
                                                            </InputGroupText>
                                                        </InputGroupAddon>
                                                        <Input
                                                            placeholder="Email"
                                                            type="text"
                                                            onFocus={e => this.setState({emailFocus: true})}
                                                            onBlur={e => this.setState({emailFocus: false})}
                                                            value={this.state.email}
                                                            onChange={this.onChangeEmail}
                                                        />
                                                    </InputGroup>
                                                    <InputGroup
                                                        className={classnames({
                                                            "input-group-focus": this.state.passwordFocus
                                                        })}
                                                    >
                                                        <InputGroupAddon addonType="prepend">
                                                            <InputGroupText>
                                                                <i className="tim-icons icon-lock-circle"/>
                                                            </InputGroupText>
                                                        </InputGroupAddon>
                                                        <Input
                                                            placeholder="Password"
                                                            type="text"
                                                            onFocus={e =>
                                                                this.setState({passwordFocus: true})
                                                            }
                                                            onBlur={e =>
                                                                this.setState({passwordFocus: false})
                                                            }
                                                            value={this.state.password}
                                                            onChange={this.onChangePassword}
                                                        />
                                                    </InputGroup>
                                                    <FormGroup check className="text-left">
                                                        <Label check>
                                                            <Input type="checkbox" value={this.state.termsAgreed}
                                                                   onChange={this.onChangeTerms}/>
                                                            <span className="form-check-sign"/>I agree to the{" "}
                                                            <a
                                                                href="#pablo"
                                                                onClick={e => e.preventDefault()}
                                                            >
                                                                terms and conditions
                                                            </a>
                                                            .
                                                        </Label>
                                                    </FormGroup>
                                                </Form>
                                            </CardBody>
                                            <CardFooter>
                                                <div className="text-muted text-center mb-3">
                                                    <Button className="btn-round text-center mb-3"
                                                            color="primary"
                                                            size="lg"
                                                            onClick={this.handleSubmit}
                                                    >
                                                        Get Started
                                                    </Button>
                                                    {this.state.redirect && (
                                                        <Redirect to={'/login-page'}/>
                                                    )}
                                                </div>
                                                <div className="text-muted text-center mb-3">
                                                    <Link to={"/login-page"}>already have an account!!</Link>

                                                </div>
                                            </CardFooter>
                                        </Card>
                                    </Col>
                                </Row>
                                <div className="register-bg"/>
                                <div
                                    className="square square-1"
                                    id="square1"
                                    style={{transform: this.state.squares1to6}}
                                />
                                <div
                                    className="square square-2"
                                    id="square2"
                                    style={{transform: this.state.squares1to6}}
                                />
                                <div
                                    className="square square-3"
                                    id="square3"
                                    style={{transform: this.state.squares1to6}}
                                />
                                <div
                                    className="square square-4"
                                    id="square4"
                                    style={{transform: this.state.squares1to6}}
                                />
                                <div
                                    className="square square-5"
                                    id="square5"
                                    style={{transform: this.state.squares1to6}}
                                />
                                <div
                                    className="square square-6"
                                    id="square6"
                                    style={{transform: this.state.squares1to6}}
                                />
                            </Container>
                        </div>
                    </div>
                    <Footer/>
                </div>
            </>
        );
    }
}

export default RegisterPage;
